body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: #000;
}

main {
	height: 100vh;
	width: 100%;
	background-color: #21D4FD;
	background-image: -webkit-linear-gradient(71deg, #21D4FD 0%, #B721FF 100%);
	background-image: -o-linear-gradient(71deg, #21D4FD 0%, #B721FF 100%);
	background-image: linear-gradient(19deg, #21D4FD 0%, #B721FF 100%);
	overflow: hidden;
	position: relative;
}

.messages  {
	height: calc(100% - 60px);
	overflow-y: scroll;
	-webkit-overflow-scrolling: touch;
}

.messages ul {
	list-style: none;
	padding-left: 15px;
}

.messages ul li {
	margin-bottom: 10px;
}

.messages ul li div {
	padding: 10px;
	display: inline-block;
	background: #fff;
	border-radius: 4px;
	max-width: 300px;
	word-break: break-word;
}

.messages ul li div > span {
	font-size: 12px;
	color: #999;
}

.messages ul li div > p {
	margin: 5px 0 0 0;
}

.msg-form {
	width: 100%;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: -ms-flexbox;
	display: flex;
	height: 60px;
	position: absolute;
	bottom: 0;
}
.msg-form > input {
	-ms-flex-positive: 1;
	    flex-grow: 1;
	font-size: 18px;
	padding-left: 15px;
	border-radius: 0 !important;
	margin: 0 !important;
	-webkit-appearance: none;
}

.msg-form > button {
	min-height: 100%;
	border-radius: 0 !important;
	background: #E63946;
    border: none;
    color: #fff;
    outline: none;
    -webkit-appearance: none;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.login-box {
	max-width: 350px;
	margin: 20px auto;
	padding: 20px;
	background: #fff;
}

.login-box input, .login-box button {
	width: 100%;
	display: block;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

.login-box input {
	padding: 10px 0 10px 10px;
}

.login-box button {
	margin-top: 5px;
	border: none;
	padding: 10px 0;
	background: #222;
	color: #fff;
}

.login-box button.fb {
	background: #3B5998;
}

.login-box button.tw {
	background: #1DA1F2;
}



